.icon-placeholder::-webkit-input-placeholder { /* WebKit browsers */
    color: #a3a3a3;
    font-family: sans-serif, 'Font Awesome 5 Free';
    font-weight: 900;
}

input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: #a3a3a3;
    font-family: sans-serif, 'Font Awesome 5 Free';
    font-weight: 900;
}

input::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: #a3a3a3;
    font-family: sans-serif, 'Font Awesome 5 Free';
    font-weight: 900;
}


.custom-collapse {
    max-height: 0;
    transition: max-height 0.2s ease-out;
    overflow: hidden;
}

.custom-collapse.show {
    max-height: 600px;
    transition: max-height 0.2s ease-in;
    overflow: scroll;
}


/* Main Grid */

:root {
    --header-height: 78px;
    --sidebar-width: 360px;
    --header-buttons-width: 200px;
}

.header {
    grid-area: header;
    overflow: hidden;
}

.header-buttons {
    grid-area: header-buttons;
    overflow: hidden;
}

.main {
    grid-area: main;
    overflow: scroll;
}

.sidebar {
    grid-area: sidebar;
    overflow-x: hidden;
    overflow-y: scroll;
}


.footer {
    grid-area: footer;
    overflow: hidden;
}

.main-grid {
    display: grid;
    grid-template-columns: auto auto var(--header-buttons-width);
    grid-template-rows: var(--header-height) auto var(--header-height) auto;
    grid-template-areas:
            "sidebar sidebar sidebar"
            "sidebar sidebar sidebar"
            "header header header-buttons"
            "main main main";
}

@media only screen and (min-width: 768px) {
    .main-grid {
        min-height: 100vh;
        grid-template-columns: var(--sidebar-width) auto auto var(--header-buttons-width);
        grid-template-rows: var(--header-height) auto;
        grid-template-areas:
                "sidebar header header header-buttons"
                "sidebar main main main";
    }

    .sidebar {
        position: sticky;
        top: 0;
        height: 100vh;
    }
}

/* Other Grids */

.variable-input-grid {
    display: grid;
    grid-gap: 4px;
    grid-template-columns: 31px minmax(50px, 60%) minmax(50px, 60%) 78px;
    grid-auto-rows: auto;
}

/* Messages */

.message-container {
    position: fixed;
    bottom: 10px;
    right: 20px;
    z-index: 2;
}

/* Custom Bootstrap components */

.list-group-item-transparent {
    color: white;
    background-color: rgba(180, 200, 255, 0.05);
}

.list-group-item-transparent.list-group-item-action:not(.active):hover {
    color: white;
    background-color: rgba(180, 200, 255, 0.15);
}

.list-group-item-transparent.active {
    border-color: rgba(213, 226, 236, 0.24);
    background-color: rgba(180, 200, 255, 0.35);
}

.btn-outline-transparent {
    border-color: #d5e2ec55;
}

.btn-outline-transparent:not(.active):hover {
    color: white;
    background-color: rgba(180, 200, 255, 0.15);
}

.btn-outline-transparent.active {
    border-color: #d5e2ec55;
    background-color: rgba(180, 200, 255, 0.4);
}

.bg-night {
    background-color: #262e32;
}

@keyframes flash {
    20%  {background-color: rgba(255, 255, 255, 0.8);}
}

.flash {
    animation-name: flash;
    animation-duration: 600ms;
    animation-timing-function: ease-out;
}
